import React, { Component } from 'react'

export default class Time extends Component {

  state = {
    operator: 0,
    active: false,
  }

  componentDidMount = () => {
    this.setState({ operator: this.props.operator_left });
  }

  render() {
    const date = new Date();
    const curDate = new Date(this.props.date);
    const d = `${date.getFullYear()}/${date.getMonth()}/${date.getDate()}`;
    const ct = `${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
    return (
      <>
        <div className="col-sm-6 col-md-3">
          {
            this.props.operator_left > 0 ?
              Date.parse(`${d} ${ct}`) > Date.parse(`${curDate.getFullYear()}/${curDate.getMonth()}/${curDate.getDate()} ${this.props.time}`) ?
                <div className="card my-1 mx-1 bg-dark border-0" title="Penuh">
                  <div className="card-body p-2 position-relative text-white"><h4 className="m-0 text-center">{this.props.time}</h4></div>
                </div>
                :
                <div onClick={this.props.onClick} id={this.props.idClock} className={`card hour-options my-1 mx-2 ${this.props.active ? 'active-time' : ''}`}>
                  <div className="card-body p-2 position-relative"><h4 className="m-0 text-center">{this.props.time}</h4> <span className="front-office shadow-sm">{this.state.operator}</span></div>
                </div>
              :
              <div className="card my-1 mx-1 bg-danger border-0" title="Penuh">
                <div className="card-body p-2 position-relative text-white"><h4 className="m-0 text-center">{this.props.time}</h4></div>
              </div>
          }
        </div>
      </>
    )
  }
}
