import React, { Component } from 'react';
import Dropzone from 'react-dropzone';
import imageCompression from 'browser-image-compression';
import { IoIosImage } from 'react-icons/io';
import { Button } from 'reactstrap';

export default class FileUploadForm extends Component {
  state = {
    img: '',
    error: false
  }

  onDrop = file => {
    if (file.length > 0) {
      let f = file[0];
      imageCompression(f, {
        maxSizeMB: 1,
        maxWidthOrHeight: 1200,
      }).then(f => {
        const reader = new FileReader();
  
        reader.onload = ev => {
          const img = ev.target.result;
          this.setState({ img }, () => this.props.onSelect({ name: this.props.file, data: img, error: false }));
        }
  
        reader.readAsDataURL(f);
      }).catch(e => {
        alert(e.toString());
      })
    }
  };

  cancel = () => {
    this.setState({ img: '' }, () => {
      this.props.onCancel(this.props.file);
    });
  }

  reject = file => {
    console.log(file);
    this.setState({ error: true });
  }

  render() {
    const { error } = this.state;
    return (
      <div className="col-md-3 h-100">
        { this.state.img !== '' &&
          <div className="card border-0 shadow h-100 my-2">
            <div className="card-body p-1 w-100">
              <Button block size="sm" color="danger" onClick={this.cancel}>Batal</Button>
            </div>
            <img src={this.state.img} alt="testing" className="card-img" />
            <div className="card-body p-1 text-center">
              <small className="text-center">{this.props.file}</small>
            </div>
          </div>
        }
        { this.state.img === '' && <Dropzone accept={['image/jpg', 'image/jpeg', 'image/png']} minSize={0} onDropRejected={this.reject} maxSize={5242880} onDrop={this.onDrop}>
          {({ getRootProps, getInputProps }) => (
            <div className="card my-2 h-100" style={{ cursor: 'pointer' }} {...getRootProps()}>
              <div className="card-body h-100 text-center">
                <input {...getInputProps()} />
                <p className={`${this.props.required ? 'text-danger' : 'text-success'} m-0`}>{this.props.required ? 'Harus di-upload' : 'File Opsional'}</p>
                <IoIosImage style={{ fontSize: 60 }} className="text-dark" />
                <p className="m-0">Seret file {this.props.file} di sini, atau klik di sini untuk memilih file</p>
                <p className="text-muted m-0 small">Format : jpg, jpeg, png</p>
                <p className="text-muted m-0 small">Maksimal 5MB</p>
                {error &&
                  <p className="text-danger m-1 small">File terlalu besar. Silakan pilih file dengan besar kurang dari 1MB</p>
                }
              </div>
            </div>
          )}
        </Dropzone>}
      </div>
    )
  }
}
