import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import Time from './Time';
import moment from 'moment';
import Flatpickr from 'react-flatpickr'
import { Spinner, Alert, Input, Button, Card, CardHeader, Collapse, CardBody, Modal, ModalBody, ModalHeader, CardTitle, Label, FormFeedback } from 'reactstrap';
import FileUploadForm from './FileUploadForm';
import Select from 'react-select';
import { FaExclamationCircle, FaCheckCircle } from 'react-icons/fa';
import { IoIosArrowBack, IoIosBookmark } from 'react-icons/io';
import { ReCaptcha } from 'react-recaptcha-v3';
import ConfirmRegister from './ConfirmRegister';
import Container from './Container';
import 'moment/locale/id';
import '../../node_modules/flatpickr/dist/themes/airbnb.css';

export default class DatePick extends Component {
  state = {
    times: [],
    time: undefined,
    dates: [],
    date: [moment(new Date()).format('YYYY-MM-DD')],
    minDate: [moment(new Date()).format('YYYY-MM-DD')],
    loading: false,
    clock: undefined,
    ready: false,
    purposes: [],
    purpose: '',
    name: '',
    nik: '',
    phone: '',
    purpose_id: 0,
    documents: [],
    requirements: [],
    captcha: '',
    queue: {},
    modal: false,
    error: false,
    submitting: false,
    errors: {
      name: false,
      nik: false,
      phone: false,
      purpose_id: false,
      captcha: false
    },
    submitErrors: {
      errors: []
    },
    collapse: false,
    clock_id: undefined,
    upload: false,
    token: '',
    key: Math.random(),
    purpose_key: Math.random(),
  }

  componentDidMount = () => {
    document.title = "Daftar Antrian";
    this.getEnabledDate();
    // this.getPurposes();
    moment.locale('id');
    setInterval(() => this.setState({ key: Math.random() }), ((60 * 3) * 1000))
  }

  componentWillReceiveProps = props => {
    if (props.date !== '' && props.clock !== '')
      this.setState({
        date: props.date,
        time: props.clock,
      })
  }

  getPurposes = () => {
    const { models: { Purpose } } = this.props;
    const { date } = this.state;
    Purpose.collection({
      limit: 999, offset: 0,
      where: {
        selected_date: moment(date[0]).format('YYYY-MM-DD')
      }
    }).then(p => {
      const purposes = p.rows.map(ps => ({ value: ps.id, label: `${ps.quota_exceeded ? '[Kuota Penuh] ' : ''}${ps.name}`, disabled: ps.quota_exceeded }))
      this.setState({ purposes });
    })
  }


  toggleClock = (time, clock, clock_id) => {
    if (time === this.state.time) {
      this.setState({ time: undefined, clock: undefined, clock_id });
    } else {
      this.setState({ time, clock, clock_id, collapse: false });
    }
  }

  getEnabledDate = () => {
    const { models: { Schedule } } = this.props;
    Schedule.collection({ attributes: ['date'] }).then(schedules => {
      // schedules.rows.reverse();
      if (schedules.rows.length > 0) {
        this.setState({ dates: schedules.rows.map(s => (s.date)), date: [schedules.rows[0].date], purposes: [], purpose: '' }, () => this.getHours(true));
      } else {
        this.setState({ error: true });
      }
    });
  }

  getHours = (loading = false) => {
    if (loading) this.setState({ loading: true })
    // this.setState({ date })
    const { models: { Schedule } } = this.props;
    const { date } = this.state;
    Schedule.single(date[0], { attributes: ['id', 'open', 'close', 'date', 'tolerance', 'processing_time', 'break_start', 'break_end', 'operator'] })
      .then(d => this.setState({ times: d.timeList, loading: false, error: false, purpose_key: Math.random() * 1000 }, () => this.getPurposes()))
      .catch(() => this.setState({ error: true, loading: false }))
  }

  toggleDate = date => {
    if (date === this.state.date) {
      this.setState({ date: undefined });
    } else {
      this.setState({ date });
    }
  }

  dateChange = (date) => {
    this.setState({ date, time: undefined, clock: undefined }, () => {
      this.getHours(true)
      this.getPurposes();
    })
  }

  next = () => {
    const { time, clock, date } = this.state;
    this.props.next({ time, clock, date })
  }

  onChange = ev => {
    const regexp = /^[a-zA-Z ,._]+$/;
    const regexp_num = /[0-9]/g;
    const { target: { name, value, type } } = ev;
    if (name === 'name') {
      if (value === "" || value.match(regexp)) {
        this.setState({ [ev.target.name]: ev.target.value });
      }
    } else if (type === 'number') {
      if (value.toString() === "" || value.match(regexp_num)) {
        this.setState({ [ev.target.name]: ev.target.value });
      }
    } else {
      this.setState({ [ev.target.name]: ev.target.value });
    }
  }

  onSelectChange = purpose => {
    const { models: { Purpose } } = this.props;
    this.setState({ purpose, purpose_id: purpose.value }, () => {
      Purpose.single(this.state.purpose.value).then(purpose => this.setState({ requirements: purpose.requirements.map(req => ({ ...req, image: false })),  }))
    });
  }

  select = data => {
    const { documents, requirements } = this.state;
    documents.push(data);
    requirements.find(req => req.name === data.name).image = true;
    this.setState({ documents, requirements });
  }

  calculateSize = (b64) => {
    const [, base64] = `${b64}`.split(',');

    const sizeInBytes = 4 * Math.ceil((base64.length / 3)) * 0.5624896334383812;
    return sizeInBytes / 1000;
  }

  cancel = name => {
    const { documents } = this.state;
    const docs = documents.filter(doc => doc.name !== name);
    this.setState({ documents: docs });
  }

  submit = () => {
    const { models: { Queue } } = this.props;
    const { date, phone, nik, name, documents, purpose_id, token, clock, requirements } = this.state;
    if (requirements.filter(req => (!req.image && req.required)).length > 0) {
      alert(`Masukkan file: \n- ${requirements.filter(req => req.required).map(req => req.name).join('\n- ')} untuk melanjutkan`)
      this.confirmRegister.close();
      this.setState({ upload: true })
    } else {
      this.confirmRegister.close();
      this.setState({ submitting: true, submitErrors: [] });
      console.log(documents)
      Queue.create({ date: moment(date[0]).format('YYYY-MM-DD'), time: clock, phone, nik, name: name.toUpperCase(), documents, purpose_id, token }).then(queue => {
        this.setState({
          queue: queue.$rawJSON,
          date: '',
          clock: undefined,
          clock_id: undefined,
          time: undefined,
          name: '',
          phone: 0,
          nik: 0,
          documents: [],
          purpose_id: undefined,
          modal: true,
          requirements: [],
          submitting: false,
          errors: { name: false, nik: false, phone: false, captcha: false },
          key: Math.random()
        })
        localStorage.setItem('queue_number', queue.queue_number);
      }).catch(({ errors }) => {
        this.setState({ submitErrors: { errors }, submitting: false, captcha: '', modal: true, key: Math.random(), error: true }, this.getHours);
      });
    }
  }

  validate = () => {
    const { phone, nik, name, purpose_id, captcha, errors, clock } = this.state;

    if (purpose_id > 0) {
      errors.purpose_id = false
    } else {
      errors.purpose_id = true
    }

    if (captcha === '') {
      errors.captcha = true;
    } else {
      errors.captcha = false;
    }

    if (name === '') {
      errors.name = true;
    } else {
      errors.name = false;
    }

    if (nik === '') {
      errors.nik = true;
    } else {
      errors.nik = false;
    }

    if (phone === '') {
      errors.phone = true;
    } else {
      errors.phone = false;
    }

    if (typeof clock === 'undefined') {
      this.setState({ collapse: true });
    }

    this.setState({ errors });
    const wrapper = document.querySelector('.wrapper');
    wrapper.scrollTop = 0;
  }

  toggleCollapse = () => {
    this.setState({ collapse: !this.state.collapse });
  }

  onCollapseOpen = () => {
    const { clock_id } = this.state;
    const wrapper = document.querySelector('div.wrapper');

    if (clock_id !== undefined) {
      const time = document.querySelector(`div#${clock_id}`);
      wrapper.scrollTo(0, time.getBoundingClientRect().y)
    }
  }

  setToken = token => {
    console.log(token)
    this.setState({ token })
  }

  render() {
    const {
      queue,
      name,
      // date,
      purpose_id,
      // clock,
      phone,
      nik,
      errors: { name: nama, nik: no, phone: p, purpose_id: tujuan },
      key,
      purpose_key
    } = this.state;
    let { times } = this.state;
    times = times.filter(time => time.operator_left > 0)
    return (
      <>
        {this.state.submitting && <LoadingSubmit />}
        <Container>
          <Card className="border-0 shadow">
            <CardHeader>
              <div ref={ref => this.cont = ref} className="d-flex flex-row justify-content-between align-items-center">
                <Link to="/" color="sm" outline className="btn btn-outline-dark btn-sm d-inline"><IoIosArrowBack /></Link>
                <CardTitle className="m-0"><IoIosBookmark size="1.5em" />&nbsp;Booking Antrian</CardTitle>
              </div>
            </CardHeader>
            <CardBody>
              <Label className="my-1">Pilih Tanggal</Label>
              {this.state.dates.length > 0 ? <Flatpickr options={{ enable: this.state.dates }} onChange={this.dateChange} className="form-control" value={this.state.date} />
                :
                this.state.error ?
                  <Input disabled value={`Booking tidak dibuka pada ${moment(this.state.date[0]).format("dddd, DD MMMM YYYY")}`} /> :
                  <Input disabled value="Mengambil tanggal" />}
              <Label className="my-1">Pilih Tujuan Mengantri</Label>
              {this.state.purposes.length > 0 ?
                <Select options={this.state.purposes} key={purpose_key} isOptionDisabled={opt => (opt.disabled)} placeholder="Pilih Tujuan" value={this.state.purpose} onChange={this.onSelectChange} />
                :
                <Input disabled placeholder="Mengambil daftar tujuan mengantri" />}
              {tujuan && <p className="small text-danger">Pilih tujuan mengantri</p>}
              {this.state.requirements.length > 0 &&
                <>
                  <Button block size="sm" onClick={() => this.setState({ upload: !this.state.upload })} className="mt-3" outline color="success">Lihat persyaratan / Upload berkas</Button>
                  <Collapse isOpen={this.state.upload}>
                    <hr />
                    {this.state.requirements.length > 0 && <p className="text-muted small">Pengantri harap membawa dokumen sebagai berikut</p>}
                    <div className="row justify-content-md-center">{this.state.requirements.map((req, i) => (<Requirement {...req} key={i} />))}</div>
                    {this.state.requirements.length > 0 && <p className="text-muted small">Atau upload berkas di sini</p>}
                    <div className="row">
                      {this.state.requirements.map((req, i) => (<FileUploadForm onCancel={this.cancel} file={req.name} onSelect={this.select} required={req.required} key={i} />))}
                    </div>
                    <hr />
                  </Collapse>
                </>
              }
              <Label className="my-1">NIK Pengantri</Label>
              <Input invalid={no} disabled={this.state.submitting} name="nik" value={this.state.nik} onChange={this.onChange} type="number" className="my-1" placeholder="NIK Pengantri" />
              {no && <FormFeedback invalid>Masukkan NIK pengantri</FormFeedback>}
              <Label className="m-0">Nama Lengkap</Label>
              <Input invalid={nama} disabled={this.state.submitting} name="name" value={this.state.name} onChange={this.onChange} placeholder="Nama pengantri" type="text" className="my-1" />
              {nama && <FormFeedback invalid>Masukkan Nama Lengkap</FormFeedback>}
              <Label className="m-0">Nomor Telepon</Label>
              <Input invalid={p} disabled={this.state.submitting} name="phone" value={this.state.phone} onChange={this.onChange} placeholder="Nomor Telepon" type="number" className="my-1" />
              {p && <FormFeedback invalid>Masukkan Nomor Telepon</FormFeedback>}
              <hr />
              {/* <p id="select" className="small text-muted my-1">Pilih Tanggal dan Jam</p> */}
              {this.state.time === undefined && <Button block outline onClick={this.toggleCollapse} color="primary">Pilih jam antri</Button>}
              <Alert color="danger" isOpen={this.state.error}>Booking tidak dibuka pada {moment(this.state.date[0]).format("dddd, DD MMMM YYYY")}</Alert>
              <Collapse onEntered={this.onCollapseOpen} isOpen={this.state.collapse}>
                <hr />
                {this.state.dates.length > 0 && <>
                  {this.state.loading ? <h4 className="text-center m-0"><Spinner color="primary" />&nbsp;Loading</h4> : <div className="row no-gutters">
                    {typeof this.state.times !== 'undefined' &&
                      times.length === 0 ? <p className="text-center m-0 text-muted">Tanggal {moment(this.state.date[0]).format('DD MMMM YYYY')} telah di-booking penuh</p>
                      :
                      <>{times.map((time, x) => (<Time active={this.state.time === x ? true : false} idClock={`time-${x}`} date={this.state.date} onClick={this.toggleClock.bind(this, x, time.time, `time-${x}`)} key={x} {...time} id={x} />))}</>
                    }
                  </div>}
                </>}
              </Collapse>
              {this.state.time !== undefined &&
                <>
                  <Button block onClick={this.toggleCollapse} className="my-2" color="dark" >{this.state.clock}</Button>
                  {/* <Card className="border-0 shadow bg-dark text-white my-2">
                    <CardBody className="p-2 text-center">{this.state.clock}</CardBody>
                  </Card> */}
                </>
              }
              <hr />
              {/* {!this.state.submitting && <Captcha invalid={c} onChange={captcha => this.setState({ captcha })} submitting={this.state.submitting} error={this.state.error} />} */}
              <ReCaptcha
                sitekey="6Lc259UUAAAAANmApCOJJboq3_AeAYTZiJqg5M6W"
                action="verify_register"
                verifyCallback={this.setToken}
                key={key}
              />
              {/* <hr /> */}
              {
                this.state.time !== undefined &&
                  name !== '' &&
                  nik !== '' &&
                  phone !== '' &&
                  purpose_id !== undefined
                  ?
                  <Button onClick={() => this.confirmRegister.open()} color="success">Daftar</Button>
                  :
                  <Button color="success" onClick={this.validate} type="button" >Daftar</Button>
              }
            </CardBody>
          </Card>
          <ConfirmRegister
            name={this.state.name}
            date={this.state.date}
            purpose={this.state.purpose.label}
            ref={ref => this.confirmRegister = ref}
            time={this.state.clock}
            onClick={this.submit}
          />
          <Modal isOpen={this.state.modal}>
            {typeof this.state.submitErrors.errors === 'undefined' ? '' :
              <ModalHeader className={`${this.state.submitErrors.errors.length > 0 ? 'bg-danger' : 'bg-success'} text-white`}>{`${this.state.submitErrors.errors.length > 0 ? 'Pendaftaran Gagal' : 'Pendaftaran Berhasil'}`}</ModalHeader>
            }
            {
              Object.keys(queue).length > 0 &&
              <ModalHeader className={`${Object.keys(queue).length === 0 ? 'bg-danger' : 'bg-success'} text-white`}>{`${Object.keys(queue).length === 0 ? 'Pendaftaran Gagal' : 'Pendaftaran Berhasil'}`}</ModalHeader>
            }
            <ModalBody>
              {
                Object.keys(queue).length > 0 && <>
                  <h1 className="m-0 text-center text-success"><FaCheckCircle size="2em" /></h1>
                  <h4 className="m-0 font-weight-light text-center">{queue.name}</h4>
                  <p className="m-0 text-muted text-center">Antrian tanggal {moment(queue.date).format('dddd, DD MMMM YYYY')}</p>
                  <hr />
                  <p className="m-0">Nomor antrian : {queue.queue_number}</p>
                  <p className="m-0">Jam : {queue.time}</p>
                  <hr />
                  <Link to="/" className="btn btn-primary">Kembali</Link>
                </>}
              {typeof this.state.submitErrors.errors === 'undefined' ? '' :
                this.state.submitErrors.errors.length === 0 ? ''
                  :
                  <>
                    <p className="my-2 text-center text-danger"><FaExclamationCircle size="6em" /></p>
                    {typeof this.state.submitErrors.errors === 'undefined' ? '' :
                      this.state.submitErrors.errors.map(e => (<Alert color="danger" className="my-2" key={e.msg}>{e.msg}</Alert>))
                    }
                    <hr />
                    <Button onClick={() => this.setState({ modal: false })} color="danger">Tutup</Button>
                  </>
              }
              {
                this.state.error &&
                <p>{this.state.errors.toString()}</p>
              }
            </ModalBody>
          </Modal>
        </Container>
      </>
    )
  }
}

const Requirement = props => (
  <div className="col-md-6">
    <Alert color="info">{props.name}</Alert>
  </div>
)

const LoadingSubmit = () => (
  <div className="loading-container">
    <Spinner color="light" size="lg" className="my-2" />
    <h5 className="m-0 text-white">Mengirim data</h5>
  </div>
)